export default () => {
  if (document.querySelector('.js-check-all') && document.querySelector('.js-row-selection')) {
    const checkAll = document.querySelector('.js-check-all');
    const allChecks = document.querySelectorAll('.js-row-selection');
    checkAll.addEventListener('change', () => {
      if (checkAll.checked) {
        for (let i = 0; i < allChecks.length; i++) {
          allChecks[i].checked = true;
        }
      } else {
        for (let i = 0; i < allChecks.length; i++) {
          allChecks[i].checked = false;
        }
      }
    });
  }

  if (document.querySelector('.js-unique-link-field')) {
    const uniqueLinkField = document.querySelector('.js-unique-link-field');
    const uniqueLink = document.querySelector('.js-unique-link');
    uniqueLinkField.style.display = 'none';
    uniqueLink.addEventListener('change', ()=> {
      if (uniqueLink.checked) {
        uniqueLinkField.style.display = 'block';
      } else {
        uniqueLinkField.style.display = 'none';
      }
    });
  }
};
