import DateTime from '../../js/vendor/tail.datetime-full.min';

export default () => {

  document.addEventListener('DOMContentLoaded', () => {
    DateTime('.js-calendar', {
      timeHours: false,
      timeMinutes: false,
      timeSeconds: false,
      timeFormat: '',
      dateFormat: 'dd-mm-YYYY',
      locale: 'ru'
    });
  });
};
