export default () => {

  const tabControls = document.querySelectorAll('.js-tab-control');
  for (let i = 0; i < tabControls.length; i++) {
    tabControls[i].addEventListener('click', () => {
      const tabContainer = tabControls[i].closest('.js-tabs');
      const tabs = tabContainer.querySelectorAll('.js-tab');
      const siblings = [].slice.call(tabControls[i].parentNode.children)
        .filter((v) => { return v !== tabControls[i]; });
      for (let j = 0; j < siblings.length; j++) {
        siblings[j].classList.remove('active');
      }
      tabs.forEach((el) => {
        el.classList.remove('active');
      });
      tabControls[i].classList.add('active');
      tabs[i].classList.add('active');
    });
  }
};
