import autosizeInput from '../../js/vendor/autosize-input';
import DateTime from '../../js/vendor/tail.datetime-full.min';

export default () => {

  if (document.querySelector('.js-task-widget')) {
    let btnCreateTask = document.querySelector('.js-create-task');
    if (!(btnCreateTask)) return;

    let taskWidget = document.querySelector('.js-task-widget');
    let btnClose = document.querySelector('.task-widget__close-button');
    let textarea = document.querySelector('.task-widget__textarea');

    btnCreateTask.addEventListener('click', () => {
      if (!btnCreateTask.classList.contains('hide')) {
        btnCreateTask.classList.add('hide');
        taskWidget.classList.add('show');
      }
    });

    btnClose.addEventListener('click', () => {
      btnCreateTask.classList.remove('hide');
      taskWidget.classList.remove('show');
    });

    let autoExpand = function (field) {
      // Reset field height
      field.style.height = 'inherit';

      // Get the computed styles for the element
      let computed = window.getComputedStyle(field);

      // Calculate the height
      let height = parseInt(computed.getPropertyValue('border-top-width'), 10)
        + parseInt(computed.getPropertyValue('padding-top'), 10)
        + field.scrollHeight
        // + parseInt(computed.getPropertyValue('padding-bottom'), 10)
        + parseInt(computed.getPropertyValue('border-bottom-width'), 10);

      field.style.height = height + 'px';

    };

    textarea.addEventListener('input', (event) => {
      if (event.target.tagName.toLowerCase() !== 'textarea') return;
      autoExpand(event.target);
    }, false);

    const dateField = document.querySelector('.task-widget__input--date');
    if (dateField) {
      autosizeInput(dateField);

      DateTime(dateField, {
        position: ".task-widget__calendar-container",    // Appends the Calendar to this Container
        startOpen: false,
        stayOpen: false,
        timeHours: false,
        timeMinutes: false,
        timeSeconds: false,
        timeFormat: '',
        dateFormat: 'dd-mm-YYYY',
        locale: 'ru'                    // Keeps the Calendar Popup Open
      });

    }

  }
};
