export default () => {

  const togglers = document.querySelectorAll('.communication-header__button');
  const communicationContent = document.querySelectorAll('.communication__content');

  if (!togglers) return;

  const clearState = () => {
    for (let i = 0; i < togglers.length; i++) {
      togglers[i].classList.remove('active');
    }

    for (let i = 0; i < communicationContent.length; i++) {
      communicationContent[i].classList.remove('show');
    }
  };

  const toggleOnClick = (evt) => {
    clearState();
    const id = evt.target.getAttribute('data-target');
    const content = document.getElementById(id);
    evt.target.classList.add('active');
    content.classList.add('show');
  };

  for (let i = 0; i < togglers.length; i++) {
    togglers[i].addEventListener('click', toggleOnClick);
  }

};
